.nvbar{
    position: absolute;
    top: 0;
    width: 100vw;
    background-color: #00B7C282;
    height: 3vw;
    padding: 0;
    align-self: center;

}

.nvbar-content{
    width:100vw;
    color: white;
    font-size: 2vw;
    font-weight: 800;
}
.nv-expanded{
    display: inline;
}

.nv-expanded-contents{
    margin: 0 5vw 0 5vw;
}
.nav-link{
    display: inline;
    color: white;
    font-size: 2vw;
    font-weight: 800;

}

a:hover{
    color: #FF9A02;
}


@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
    .nvbar{
        height: 10vw;
    }
    .nvbar-content{

        font-size: 6vw;

    }
}