@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400;700;800&display=swap');
.App {
  text-align: center;
  font-family: 'Sen', sans-serif;
}

#bg-homepage{
  width: 100vw;
  height: 100vh
}

.title {
  color: white;
  font-size: 11vw;

}

.desc-title{
  color: #FF9A02;
  font-size: 2.2vw;
  width: 44%;
  font-weight: bold;
  margin-left:28%;
}
.title-container{
  position: absolute;
  top: 33vh;
  width: 100%;
  line-height: 1;
}

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
  .title {
    color: white;
    font-size: 20vw;

  }

  .desc-title{
    color: #FF9A02;
    font-size: 4vw;
    width: 90%;
    font-weight: bold;
    margin-left:  5%;
  }
  .title-container{
    position: absolute;
    top: 33vh;
    width: 100%;
    line-height: 1;
  }
}